.Toolbar {
    height: 56px;
    width: 100%;
    border: 1px solid #868680;
    border-style: none none solid none;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #242424;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
    padding-right: 0;
}

.ToolbarText {
    float: left;
    color: white;
    padding: 18px 0px;
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
}

.Toolbar nav {
    height: 100%;
    width: 100%;
}

.Logo {
    height: 80%;
}

@media (max-width: 600px) {
    .DesktopOnly {
        display: none;
    }
    .ToolbarText {
        font-size: 14px;
    }
}


