.SideDrawer {
    position: fixed;
    overflow: auto;
    width: 280px;
    max-width: 70%;
    height: calc(100% - 56px);
    left: 0;
    top: 56px;
    z-index: 1;
    background-color: #424240;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    -o-transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.SideDrawerList {
    width: 100%;
    position: relative;
    list-style-type: none;
    left: 0px;
    padding: 0;
    margin: 0;
}

.SideDrawerAction {
    width: 100%;
    text-align: left;
    /* min-height: 32px; */
    padding: 12px 0px;
    color: white;
    font-size: 14px;
    border: 1px solid white;
    border-style: none none solid none;
    cursor: pointer;
    background: #5b5b58;
}

.SideDrawerAction p {
    padding-left: 48px;
    font-weight: bold;
}

.SideDrawerAction div {
    position: relative;
    width: 24px;
    height: 24px;
    left: 12px;
    top: 0px;
    margin: 0px;
    float: left;
}

.SideDrawerAction:hover {
    background: #292927;
}

.SideDrawerTop {
    top: 0px;
    background: #424240;
    z-index: 2;
    position: -webkit-sticky;
    position: sticky;
}

.SideDrawerTitle {
    padding: 11px 16px;
    font-weight: 700;
    color: white;
    font-size: 13px;
    line-height: 180%;
    text-align: center;
    border: 1px solid white;
    border-style: none none solid none;
    margin-bottom: 0px;
    text-transform: uppercase;
}

@media (max-width: 400px) {
    .SideDrawer {
        display: none;
    }
}


